import * as React from 'react';
import { Link } from 'gatsby';

const OurTeamPage = () => {
  return (
    <main className="bg-gradient-to-r from-blue-500 to-purple-500 text-white min-h-screen flex flex-col justify-center items-center">
      <h1 className="text-4xl font-bold mb-4">Our Team</h1>
      <p className="text-lg text-center max-w-md">
        Hey! Our website is currently under construction but if you have any questions, we'd love to hear from you.
        <br />
        <Link to="mailto:support@verdantvalleyslawn.com" className="text-blue-300 hover:underline">
          support@verdantvalleyslawn.com
        </Link>
      </p>
    </main>
  );
};

export const Head = () => (
  <>
    <title>Our Team</title>
    <meta name="description" content="Description content" />
  </>
);

export default OurTeamPage;